.header {
  font-family: "Montserat", sans-serif;
  background-color: #F9E576;
  color: #000000;
  padding-top: 35px;
}
.header__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__button {
  color: #000000;
  text-transform: uppercase;
  background-color: transparent;
  font-size: 15px;
  line-height: 18px;
  font-weight: 600;
  padding: 16px 26px;
  border-radius: 10px;
  border: 2px solid #000000;
  text-decoration: none;
  cursor: pointer;
}

.menu {
  display: flex;
  justify-content: space-between;
  list-style: none;
  font-size: 16px;
  line-height: 20px;
}
.menu__item {
  position: relative;
  font-size: 16px;
  font-weight: 20px;
}
.menu__link {
  margin-right: 70px;
  padding: 15px 0;
  text-decoration: none;
  color: #000000;
  position: relative;
}
.menu__item:hover > .menu__link::after, .menu__item:focus > .menu__link::after, .menu__item:active > .menu__link::after, .menu__item.active > .menu__link::after {
  content: "";
  padding: 4px;
  display: inline-block;
  background: url("../img/Arrow-down.png") no-repeat;
  margin-left: 10px;
}

.popup-menu__link {
  color: #000000;
  font-size: 13px;
  line-height: 20px;
  padding-top: 16px;
  text-decoration: none;
  position: relative;
}
.popup-menu__link:hover, .popup-menu__link:focus, .popup-menu__link:active {
  color: #F9E576;
}

.popup-menu__item:hover > .popup-menu__link,
.popup-menu__item:focus > .popup-menu__link,
.popup-menu__item:active > .popup-menu__link,
.popup-menu__item.active > .popup-menu__link {
  color: #F9E576;
}

.popup-menu {
  padding: 40px 0 0;
  width: 200px;
  margin: 0;
  list-style: none;
  transition: 0.3s;
  display: none;
}
.popup-menu__item {
  padding-top: 16px;
  text-align: center;
}
.popup-menu.abs {
  display: block;
  height: 0;
  overflow: hidden;
  padding: 35px 9px;
  position: absolute;
  top: -15px;
  margin-top: 17px;
  left: -75px;
  opacity: 0;
  cursor: pointer;
  background: url("../img/Vacancies_open.png") no-repeat;
}
.popup-menu.abs:hover, .popup-menu.abs:focus, .popup-menu.abs:active {
  opacity: 1;
  z-index: 10;
}

.menu__item:hover > .popup-menu.abs,
.menu__item:focus > .popup-menu.abs,
.menu__item:active > .popup-menu.abs {
  opacity: 1;
  height: auto;
  z-index: 10;
}

.main {
  font-family: "Montserat", sans-serif;
  padding-top: 80px;
  padding-bottom: 90px;
  background-color: #F9E576;
  position: relative;
}
.main__title {
  font-size: 56px;
  line-height: 84px;
  font-weight: 700;
  max-width: 961px;
  margin: 0 auto;
  text-align: center;
}
.main__options {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 481px;
  margin: 0 auto;
  margin-bottom: 40px;
}
.main__link {
  display: flex;
  align-items: center;
}
.main .link-content {
  text-decoration: none;
  color: #000000;
}
.main .link-content > span {
  font-weight: 700;
}
.main__button {
  background-color: #000000;
  color: #f6f6f6;
  padding: 17px 28px;
  font-size: 15px;
  line-height: 18px;
  border-radius: 10px;
  text-decoration: none;
  text-transform: uppercase;
  display: flex;
  margin: 0 auto;
  max-width: 242px;
  justify-content: center;
}
.main__img-laptop {
  position: absolute;
  left: 0%;
  bottom: -15%;
}
.main__img-helix {
  position: absolute;
  bottom: -13%;
  right: 15%;
}
.main__img-pyramid {
  position: absolute;
  bottom: 52%;
  left: 3%;
}
.main__img-notebook {
  position: absolute;
  right: -1%;
  bottom: 15%;
}

.social-network {
  background-color: #FAFAFA;
  margin: 0;
}
.social-network__list {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 90px 110px;
  margin: 0;
}